
#mm-prv-wdg {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.mm-prv-agree-submit-container {
  text-align: left;
}

.privacy-paragraphs .paragraph-title {
  font-size: 12px;
  text-align: left;
  padding: 4px;
  font-weight: bold;
}
.privacy-paragraphs .scroll-warning {
  padding: 8px;
  margin-bottom: 8px;
  color: red;
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  border: 1px solid #e1e1e1;
  background-color: #fcecec;
  border-radius: 2px;
}

.privacy-scroller {
  text-align: left;
  padding: 2px 10px;
  font-size: 12px;
  min-height: 50px;
}
.scroll-area {
  border: 1px solid silver;
  position: relative;
  margin: auto;
  width: auto;
  min-height: 50px;
}

.privacy-flag {
  text-align: left;
  font-size: 12px;
}
.privacy-flag td {
    vertical-align: center;
}
.privacy-flag .privacy-flag-checkbox {
    width: 20px;
}
.privacy-flag.not-scrolled {
  color: silver;
}
